import React from 'react';
import { Element } from 'react-scroll'
import SayHelloBackgroundImg from './../assets/img/demos/resume/contact/contact-bg-1.jpg';

class SayHello extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			isError: false 
		};

		this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleSubmit(e){
		e.preventDefault();
		this.setState({
			isError: true
		});
	}

    render() {

        const SayHelloBackground = {
            background: `url(${SayHelloBackgroundImg})`,
            backgroundSize: "cover"
		};

        return (
			<Element name="say-hello">
				<div id="say-hello" className="container-fluid">
				<div className="row">
					<div className="col-lg-6 p-0">
						<section className="section section-no-border bg-color-primary h-100 m-0">
							<div className="row justify-content-end m-0">
								<div className="col-half-section col-half-section-right mr-3">
									
									<h2 className="text-color-quaternary text-uppercase font-weight-extra-bold">Say Hello</h2>
									<form id="contactForm" className="contact-form custom-form-style" onSubmit={this.handleSubmit}>
										<div className="contact-form-success alert alert-success d-none mt-4" id="contactSuccess">
											<strong>Success!</strong> Your message has been sent to us.
										</div>
										<div className="contact-form-error alert alert-danger d-none mt-4" id="contactError">
											<strong>Error!</strong> There was an error sending your message.
											<span className="mail-error-message text-1 d-block" id="mailErrorMessage"></span>
										</div>
										
										<div className="form-content">
											<div className="form-control-custom">
												<input type="text" className="form-control" name="name" placeholder="Your Name *" data-msg-required="This field is required." id="name" required="" />
											</div>
											<div className="form-control-custom">
												<input type="text" className="form-control" name="subject" placeholder="Subject *" data-msg-required="This field is required." id="subject" required="" />
											</div>
											<div className="form-control-custom">
												<textarea maxLength="5000" data-msg-required="Please enter your message." rows="10" className="form-control" name="message" placeholder="Message *" id="message" required="" aria-required="true"></textarea>
											</div>
											<input type="submit" className="btn btn-quaternary text-color-light text-uppercase font-weight-semibold outline-none custom-btn-style-2 custom-border-radius-1" value="Submit" />

											{
												this.state.isError ? 
													<div className="contact-form-error alert alert-danger d-none1 mt-4 cust-error" id="contactError">
														<strong>Error!</strong> There was an error sending your message.
														<span className="mail-error-message text-1 d-block" id="mailErrorMessage"></span>
													</div>
												 	: ''
											}

										</div>
									</form>

								</div>
							</div>
						</section>
					</div>
					<div className="col-lg-6 p-0">
						<section className="section section-no-border h-100 m-0" style={SayHelloBackground}>
							<div className="row m-0">
								<div className="col-half-section col-half-section-left ml-3">
									<a href="mailto:azahmed07860@gmail.com" className="text-decoration-none">
										<span className="feature-box custom-feature-box align-items-center mb-4">
											<span className="custom-feature-box-icon">
												<i className="icon-envelope icons text-color-light"></i>
											</span>
											<span className="feature-box-info">
												<span className="custom-label font-weight-semibold text-uppercase custom-text-color-1">Email</span>
												<strong className="font-weight-light text-color-light custom-opacity-effect-1">azahmed07860@gmail.com</strong>
											</span>
										</span>
									</a>
									<a href="tel:+918655006102" className="text-decoration-none">
										<span className="feature-box custom-feature-box align-items-center mb-4">
											<span className="custom-feature-box-icon">
												<i className="icon-phone icons text-color-light"></i>
											</span>
											<span className="feature-box-info">
												<span className="custom-label font-weight-semibold text-uppercase custom-text-color-1">Phone</span>
												<strong className="font-weight-light text-color-light custom-opacity-effect-1">+91 8655006102</strong>
											</span>
										</span>
									</a>
									<a href="skype:azahmed07860?chat" className="text-decoration-none">
										<span className="feature-box custom-feature-box align-items-center mb-4">
											<span className="custom-feature-box-icon">
												<i className="icon-social-skype icons text-color-light"></i>
											</span>
											<span className="feature-box-info">
												<span className="custom-label font-weight-semibold text-uppercase custom-text-color-1">Skype</span>
												<strong className="font-weight-light text-color-light custom-opacity-effect-1">azahmed07860</strong>
											</span>
										</span>
									</a>
									<span className="feature-box custom-feature-box align-items-center sh-follow-me">
										<span className="custom-feature-box-icon">
											<i className="icon-share icons text-color-light"></i>
										</span>

										<a href="https://www.linkedin.com/in/azeem-ahmad/" target="_blank" rel="noopener noreferrer" className="d-flex text-decoration-none">
											<span className="feature-box-info">
												<span className="custom-label font-weight-semibold text-uppercase custom-text-color-1">Follow me</span>
												<strong className="font-weight-light text-color-light custom-opacity-effect-1">Linkedin</strong>
											</span>
										</a>

										{/* <a href="https://www.linkedin.com/in/azeem-ahmad/" className="d-flex text-decoration-none">
											<span className="feature-box-info p-0 mt-2">
												<strong className="font-weight-light text-color-light custom-opacity-effect-1">Linkedin</strong>
											</span>
										</a> */}
										<a href="https://github.com/azeem-ahmad/" target="_blank" rel="noopener noreferrer" className="d-flex text-decoration-none">
											<span className="feature-box-info custom-both-side-border mt-2">
												<strong className="font-weight-light text-color-light custom-opacity-effect-1">GitHub</strong>
											</span>
										</a>

										<a href="https://wa.me/918655006102" target="_blank" rel="noopener noreferrer" className="d-flex text-decoration-none">
											<span className="feature-box-info custom-right-side-border mt-2">
												<strong className="font-weight-light text-color-light custom-opacity-effect-1">WhatsApp</strong>
											</span>
										</a>


										<a href="https://www.instagram.com/__azeem__ahmad" target="_blank" rel="noopener noreferrer" className="d-flex text-decoration-none">
											<span className="feature-box-info p-0 mt-2">
												<strong className="font-weight-light text-color-light custom-opacity-effect-1 lh-28px">Instagram</strong>
											</span>
										</a>

										{/* <a href="http://www.facebook.com" className="d-flex text-decoration-none">
											<span className="feature-box-info">
												<span className="custom-label font-weight-semibold text-uppercase custom-text-color-1">Follow me</span>
												<strong className="font-weight-light text-color-light custom-opacity-effect-1">Facebook</strong>
											</span>
										</a> */}

									</span>
								</div>
							</div>
						</section>
					</div>
				</div>
			</div>
			</Element>
        )
    }
}

export default SayHello;