import React from 'react';
import { Link } from 'react-scroll'

class Header extends React.Component {
    render() {
        return (
            <header id="header" className="header-floating-icons" data-plugin-options="{'stickyEnabled': false, 'stickyEnableOnBoxed': false, 'stickyEnableOnMobile': false, 'stickyStartAt': 0, 'stickySetTop': '60px', 'stickyChangeLogo': false}">
				<div className="header-body">
					<div className="header-container container">
						<div className="header-row">
							<div className="header-column">
								<div className="header-row">
									<div className="header-nav pt-1">
										<div className="header-nav-main">
											<nav className="wrapper-spy collapse">
												<ul className="nav" id="mainNav">
													<li>
														<Link to="about-me" spy={true} smooth={true} duration={500} data-hash="" className="nav-link text-color-dark bg-color-primary">
															<i className="icon-home icons"></i>
															<span className="custom-tooltip">About Me</span>
														</Link>
													</li>
													<li>
														<Link to="experience" spy={true} smooth={true} duration={500} data-hash="" className="nav-link text-color-dark bg-color-primary">
															<i className="icon-briefcase icons"></i>
															<span className="custom-tooltip">Experience</span>
														</Link>
													</li>
													<li>
														<Link to="education" spy={true} smooth={true} duration={500} data-hash="" className="nav-link text-color-dark bg-color-primary">
															<i className="icon-graduation icons"></i>
															<span className="custom-tooltip">Education</span>
														</Link>
													</li>
													<li>
														<Link to="skills" spy={true} smooth={true} duration={500} data-hash="" className="nav-link text-color-dark bg-color-primary">
															<i className="icon-diamond icons"></i>
															<span className="custom-tooltip">Skills</span>
														</Link>
													</li>
													<li>
														<Link to="portfolio" spy={true} smooth={true} duration={500} data-hash="" className="nav-link text-color-dark bg-color-primary">
															<i className="icon-eye icons"></i>
															<span className="custom-tooltip">Portfolio</span>
														</Link>
													</li>
													{/* <li>
														<Link to="recommendations" spy={true} smooth={true} duration={500} data-hash="" className="nav-link text-color-dark bg-color-primary">
															<i className="icon-pencil icons"></i>
															<span className="custom-tooltip">Recommendations</span>
														</Link>
													</li>
													<li>
														<Link to="blog" spy={true} smooth={true} duration={500} className="nav-link text-color-dark bg-color-primary">
															<i className="icon-bubbles icons"></i>
															<span className="custom-tooltip">Blog</span>
														</Link>
													</li> */}
													<li>
														<Link to="say-hello" spy={true} smooth={true} duration={500} data-hash="" className="nav-link text-color-dark bg-color-primary">
															<i className="icon-envelope-open icons"></i>
															<span className="custom-tooltip">Say Hello</span>
														</Link>
													</li>
												</ul>
											</nav>
										</div>
										<button className="btn header-btn-collapse-nav" data-toggle="collapse" data-target=".header-nav-main nav">
											<i className="fas fa-bars"></i>
										</button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</header>
        )
    }
}

export default Header;