import React from 'react';
import { Element } from 'react-scroll'

class Education extends React.Component {
    render() {

        let Education = [
            {
                college: 'Y.M.T. College Of Management',
                course: 'MCA',
                year: '2013-2016'
            },
            {
                college: 'Akbar Peerbhoy College',
                course: 'B.Sc. (Computer Science)',
                year: '2010-2013'
            },
            {
                college: 'Bandra Anjuman-I-Islam Junior College',
                course: 'H.S.C',
                year: '2010'
            },
            {
                college: 'Green Bombay Urdu High School',
                course: 'S.S.C',
                year: '2008'
            }
            
        ]

        return (
			<Element name="education">
            <section id="education" className="section section-no-border custom-bg-color-1 m-0">
            <div className="container">
                <div className="row">
                    <div className="col">
                        <h2 className="text-color-quaternary text-uppercase font-weight-extra-bold m-0">Education</h2>
                        <div className="owl-carousel nav-bottom custom-dots-style-1 mb-0" data-plugin-options="{'items': 1, 'loop': false, 'dots': true, 'nav': false}">
                            <div className="row">

                                {
                                    Education.slice(0, 2).map((item, index) => {
                                        return (
                                            <div key={index} className="col-lg-6 col-sm-6 pb-4">
                                                <div className="custom-box-details-2 bg-color-light custom-box-shadow-3">
                                                    <i className="icon-graduation icons text-color-primary"></i>
                                                    <h4 className="font-weight-semibold text-color-dark m-0">{item.college}</h4>
                                                    <p className="custom-text-color-2 mb-1">{item.course}</p>
                                                    <strong className="text-color-primary">{item.year}</strong>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                                
                            </div>
                            <div className="row">
                                {
                                    Education.slice(2, 4).map((item, index) => {
                                        return (
                                            <div key={index} className="col-lg-6 col-sm-6 pb-4">
                                                <div className="custom-box-details-2 bg-color-light custom-box-shadow-3">
                                                    <i className="icon-graduation icons text-color-primary"></i>
                                                    <h4 className="font-weight-semibold text-color-dark m-0">{item.college}</h4>
                                                    <p className="custom-text-color-2 mb-1">{item.course}</p>
                                                    <strong className="text-color-primary">{item.year}</strong>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            

                        </div>
                    </div>
                </div>
            </div>
        </section>
        </Element>
        )
    }
}

export default Education;