import React from 'react';

class Footer extends React.Component {
    render() {
		
		const BrandName = "<Azeem />"

        return (
			<footer id="footer" className="m-0 p-0">
				<div className="footer-copyright bg-color-light m-0">
					<div className="container">
						<div className="row">
							<div className="col-lg-12 m-0 text-center">
								<p className="custom-text-color-2">Made with <i className="fa fa-heart"></i> By {BrandName}</p>
							</div>
							{/* <div className="col-lg-12 m-0">
								<p className="custom-text-color-2"> Copyright 2020. All Rights Reserved.</p>
							</div> */}
						</div>
					</div>
				</div>
			</footer>
            
        )
    }
}

export default Footer;